import { Component, Input, OnInit } from '@angular/core';
import {
  ExchangeNumberEntity,
  RemittancesInteractor,
  RemittancesRepository
} from '@nequi/features/remittances/core';
import { BottomSheetController } from '@nequi/ui';

@Component({
  selector: 'app-bottomsheet',
  templateUrl: './bottomsheet.component.html',
  styleUrls: ['./bottomsheet.component.scss'],
  providers: [
    {
      provide: RemittancesInteractor,
      deps: [RemittancesRepository],
      useClass: RemittancesInteractor,
    },
  ],
})
export class BottomsheetComponent implements OnInit {
  @Input() public title: string;
  public isLoading: boolean;
  public reloadError: string;
  public remittancesList: ExchangeNumberEntity[];

  constructor(
    private bottomSheetController: BottomSheetController,
    private remittancesInteractor: RemittancesInteractor
  ) {}

  public ngOnInit() {
    this.getNumeralChanges();
  }

  public hide() {
    this.bottomSheetController.dismiss();
  }

  public reload() {
    this.reloadError = '';
    this.getNumeralChanges();
  }

  public selectItem(item: ExchangeNumberEntity) {
    this.bottomSheetController.dismiss({
        exchangeNumber: item.code,
    });
  }

  public async getNumeralChanges() {
    try {
      this.isLoading = true;
      this.remittancesList = await this.remittancesInteractor.getRemittanceExchangeNumbers();
      this.isLoading = false;
    } catch (error) {
      this.isLoading = false;
      this.reloadError = error.message;
    }
  }
}
