import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons';

import { RemittancesConfig } from '../config/remittances.config';
import { FormPage } from './pages/form/form.page';
import { TermsPage } from './pages/terms/terms.page';

const routes: Routes = [
  {
    path: 'form',
    component: FormPage,
  },
  {
    path: 'terms',
    component: TermsPage,
  },
  {
    path: 'profile',
    loadChildren: () =>
      import('@nequi/features/profile/presentation').then((m) => m.FeaturesProfileModule),
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(I18nModuleResolver.setup(routes, [RemittancesConfig.i18n.moduleKey])),
  ],
  exports: [RouterModule],
})
export class FeaturesRemittancesRoutingModule {}
