import { Component, Injector, ViewChild } from '@angular/core';
import { CoreException } from '@nequi/commons/exceptions';
import {
  AnalyticsInteractor,
  AnalyticsType,
  BarcodeInformationEntity,
  FormId,
  IncomeAndExpensesEntity,
  IncomeExpensesEntity,
  LocationEntity,
  LoggerInteractor,
  ModuleCode,
  ModuleName,
  OccupationEntity
} from '@nequi/features/commons/core';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { ModuleConfigProvider, TranslateProvider } from '@nequi/features/commons/providers';
import { LockedInSarlaftException, ModuleInfoInteractor } from '@nequi/features/profile/core';
import {
  CompletedModuleData,
  ModuleListOrganism,
  OccupationService
} from '@nequi/features/profile/presentation';
import { ErrorCode } from '@nequi/features/remittances/core';
import { CardModalService, LoadingService, ModalButtonColor, ToastService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { openUrl, stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { RemittancesConfigInfo } from './form.config';

@Component({
  selector: 'nequi-remittances',
  templateUrl: './form.page.html',
})
export class FormPage extends FeatureBasePage {
  @ViewChild('nequiRemittancesModuleList')
  private moduleListOrganism: ModuleListOrganism;

  public config = RemittancesConfigInfo;
  public isFormIncomplete = true;
  public factoryCompleteModule;
  private locationData: LocationEntity;
  private occupationData: OccupationEntity;
  private moduleListData: CompletedModuleData[];
  private incomeAndExpensesData: IncomeExpensesEntity;

  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    private moduleConfigProvider: ModuleConfigProvider,
    private moduleInfoInteractor: ModuleInfoInteractor,
    private loggerInteractor: LoggerInteractor,
    private toastService: ToastService,
    private cardModalService: CardModalService,
    private alertService: AlertService,
    private loadingService: LoadingService,
    private occupationService: OccupationService,
    private analyticsInteractor: AnalyticsInteractor
  ) {
    super(injector);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
  }

  public async onCompletedRequestData(modules: CompletedModuleData[]) {
    this.isFormIncomplete = false;
    this.moduleListData = modules;

    this.locationData = this.moduleListOrganism.getModuleData<LocationEntity>(
      this.moduleListData,
      ModuleCode.LOCATION
    );

    this.occupationData = this.moduleListOrganism.getModuleData<OccupationEntity>(
      this.moduleListData,
      ModuleCode.OCCUPATION
    );

    this.incomeAndExpensesData = this.moduleListOrganism.getModuleData<IncomeExpensesEntity>(
      this.moduleListData,
      ModuleCode.INCOME_EXPENSES
    );
  }

  public openHelpOption() {
    this.openExternalUrl(this.config.routes.help);
  }

  public goToTerms() {
    this.validateSarlaft();
  }

  public async showRemittancesModalDecline() {
    const modalCancel = this.translateProvider.get(this.config.i18n.declineRemittances);

    await this.cardModalService.show({
      title: modalCancel.title,
      imgPath: modalCancel.image,
      content: modalCancel.content,
      buttons: [
        {
          text: modalCancel.buttons.accept,
          handler: () => {
            this.navigatorProvider.navigateForward(this.config.routes.currentUrl);
          },
        },
        {
          color: ModalButtonColor.MIDNIGHT,
          text: modalCancel.buttons.cancel,
          handler: async () => {
            this.setAnalyticsDeclineRemittances();
            this.navigatorProvider.navigateRoot(this.config.routes.home);
          },
        },
      ],
    });
  }

  public async showReceiveRemittancesModal() {
    const modalAccept = this.translateProvider.get(this.config.i18n.receiveRemittanes);

    await this.cardModalService.show({
      title: modalAccept.title,
      imgPath: modalAccept.image,
      content: modalAccept.content,
      buttons: [
        {
          text: modalAccept.buttons.accept,
          handler: () => {
            this.navigatorProvider.navigateForward(this.config.routes.currentUrl);
          },
        },
        {
          color: ModalButtonColor.MIDNIGHT,
          text: modalAccept.buttons.cancel,
          handler: async () => {
            this.showRemittancesModalDecline();
          },
        },
      ],
    });

    this.setAnalyticsIntentionReceive();
  }

  public markAsCompleteModule(module: CompletedModuleData) {
    switch (module.type) {
      case ModuleCode.BARCODE_INFORMATION:
        this.documentCompleteHandler(module.data as BarcodeInformationEntity);
        break;
      case ModuleCode.LOCATION:
        this.locationCompletedHandler(module.data as LocationEntity);
        break;
      case ModuleCode.OCCUPATION:
        this.occupationCompletedHandler(module.data as OccupationEntity);
        break;
      case ModuleCode.INCOME_EXPENSES:
        this.yourIncomeCompleteHandler(module.data as IncomeAndExpensesEntity);
        break;
    }
  }

  private async validateSarlaft() {
    const occupationCode = this.occupationData?.ciiuCode?.key
      ? this.occupationData?.ciiuCode?.key
      : this.occupationData?.occupation?.name.toUpperCase();

    try {
      await this.loadingService.show();
      await this.moduleInfoInteractor.calculateClientRisk({
        cityCode: this.locationData?.city?.code,
        countryCode: this.locationData.country?.isoCode,
        incomes: this.incomeAndExpensesData?.incomeExpenses?.income?.max,
        occupationCode: occupationCode,
      });

      await this.loadingService.hideWithoutFeedback();
      this.navigatorProvider.navigateForward(this.config.routes.termAndCondition);
    } catch (error) {
      await this.loadingService.hideWithoutFeedback();
      this.manageErrors(error);
    }
  }

  private async manageErrors(error: CoreException) {
    const balanceErrors = {
      [LockedInSarlaftException.name]: () => this.showSarlaftAlert(),
    };

    if (error.code === ErrorCode.UNDERWEIGHT_SARLAFT) {
      this.showSarlaftAlert();

      return;
    }

    if (balanceErrors[error.constructor.name]) {
      return balanceErrors[error.constructor.name]();
    }

    await this.toastService.showError(error.message);
  }

  private setAnalyticsDeclineRemittances() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.declineRemittances.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.declineRemittances.params.cleverTap,
          [getFormattedDateTimes()]
        ),
      },
    });
  }

  private setAnalyticsIntentionReceive() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.intentionOfReceive.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.intentionOfReceive.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.intentionOfReceive.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.intentionOfReceive.params.cleverTap,
          [getFormattedDateTimes()]
        ),
      },
    });
  }

  private async documentCompleteHandler(document: BarcodeInformationEntity) {
    try {
      await this.moduleInfoInteractor.saveModuleInfo<BarcodeInformationEntity>({
        code: ModuleCode.BARCODE_INFORMATION,
        data: { key: ModuleName.BARCODE_INFORMATION, value: document },
        formId: FormId.REMITTANCES,
      });

      this.moduleListOrganism.finish();
    } catch (error) {
      this.loggerInteractor.error(error);

      await this.toastService.showError(error.message);
    }
  }

  private async locationCompletedHandler(location: LocationEntity) {
    try {
      await this.moduleInfoInteractor.saveModuleInfo<LocationEntity>({
        code: ModuleCode.LOCATION,
        data: { key: ModuleName.LOCATION, value: location },
        formId: FormId.REMITTANCES,
      });

      this.moduleListOrganism.finish();
    } catch (error) {
      this.loggerInteractor.error(error);

      await this.toastService.showError(error.message);
    }
  }

  private async occupationCompletedHandler(occupation: OccupationEntity) {
    occupation = {...occupation, formId: FormId.REMITTANCES};

    try {
      await this.moduleInfoInteractor.saveModuleInfo<OccupationEntity>({
        code: ModuleCode.OCCUPATION,
        data: { key: ModuleName.OCCUPATION, value: occupation },
        formId: FormId.REMITTANCES,
      });

      this.moduleListOrganism.finish();
    } catch (error) {
      this.loggerInteractor.error(error);

      await this.loadingService.hideWithoutFeedback();
      this.occupationService.processErrors(error, occupation);
    }
  }

  private async yourIncomeCompleteHandler(incomeAndExpenses: IncomeAndExpensesEntity) {
    try {
      await this.moduleInfoInteractor.saveModuleInfo<IncomeAndExpensesEntity>({
        code: ModuleCode.INCOME_EXPENSES,
        data: { key: ModuleName.INCOME_EXPENSES, value: incomeAndExpenses },
        formId: FormId.REMITTANCES,
      });

      this.moduleListOrganism.finish();
      this.isFormIncomplete = false;
    } catch (error) {
      this.loggerInteractor.error(error);

      await this.toastService.showError(error.message);
    }
  }

  private async showSarlaftAlert() {
    await this.alertService.showByI18n({
      i18nKey: this.config.i18n.controlListError,
      handlers: {
        accept: () => {
          this.navigatorProvider.navigateRoot(this.config.routes.home);
        },
      },
      backdropDismiss: false,
    });
  }

  private openExternalUrl(url: string) {
    openUrl(url);
  }
}
