import { Component, Injector } from '@angular/core';
import { ModuleConfigProvider, TranslateProvider } from '@nequi/features/commons';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { RemittancesInteractor } from '@nequi/features/remittances/core';
import { PinChallengeModalService } from '@nequi/features/security/auth/presentation';
import { BottomSheetController, CardModalService, LoadingService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { BottomsheetComponent } from '../../components/bottomsheet/bottomsheet.component';
import { TermsConfig } from './terms.config';

const TERMS_AND_CONDITION_STATUS = 'ACCEPT';

@Component({
  selector: 'nequi-remittances-terms',
  templateUrl: './terms.page.html',
  styleUrls: ['./terms.page.scss'],
})
export class TermsPage extends FeatureBasePage {
  public config = TermsConfig;

  constructor(
    injector: Injector,
    private moduleConfigProvider: ModuleConfigProvider,
    private cardModalService: CardModalService,
    private analyticsInteractor: AnalyticsInteractor,
    private remittancesInteractor: RemittancesInteractor,
    private translateProvider: TranslateProvider,
    private loadingService: LoadingService,
    private pinChallengeModalService: PinChallengeModalService,
    private bottomSheetController: BottomSheetController
  ) {
    super(injector);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
  }

  public async remittancesModalSuccess() {
    const modalSuccess = this.translateProvider.get(this.config.i18n.successReceiveRemittances);

    await this.cardModalService.show({
      title: modalSuccess.title,
      imgPath: modalSuccess.image,
      content: modalSuccess.content,
      buttons: [
        {
          text: modalSuccess.button,
          handler: () => {
            this.navigatorProvider.navigateRoot(this.config.routes.home);
          },
        },
      ],
    });
  }

  public async remittancesModalError() {
    const modalError = this.translateProvider.get(this.config.i18n.errorReceiveRemittances);

    await this.cardModalService.show({
      title: modalError.title,
      imgPath: modalError.image,
      content: modalError.content,
      buttons: [
        {
          text: modalError.button,
          handler: () => {
            this.navigatorProvider.navigateRoot(this.config.routes.home);
          },
        },
      ],
    });
  }

  public async goToNumeralChanges() {
    this.showBottomSheet();
  }

  private async showBottomSheet() {
    const bottomSheet = await this.bottomSheetController.create({
      component: BottomsheetComponent,
      isAutoHeight: true,
      componentProps: {
        title: this.translateProvider.get(this.config.i18n.bottomSheetTitle),
      },
    });

    await bottomSheet.open();

    const { data } = await bottomSheet.onDidDismiss();

    if (data) {
      this.enrollUserToReceiveRemitances(data.exchangeNumber);
    }
  }

  private async enrollUserToReceiveRemitances(exchangeNumber: string) {
    const pinChallengeMessage = this.translateProvider.get(this.config.i18n.pinChallengeMessage);

    const isValid = await this.pinChallengeModalService.show(pinChallengeMessage);

    const params = {
      exchangeNumber,
      termsAndConditionStatus: TERMS_AND_CONDITION_STATUS,
    };

    if (!isValid) {
      return;
    }

    try {
      await this.loadingService.show();
      await this.remittancesInteractor.receiveRemittances(params);
      await this.loadingService.hideWithoutFeedback();
      this.setAnalyticsSucessReceive();
      this.remittancesModalSuccess();
    } catch (error) {
      await this.loadingService.hideWithoutFeedback();
      this.remittancesModalError();
    }
  }

  private setAnalyticsSucessReceive() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.successReceiveRemittances.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.successReceiveRemittances.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.successReceiveRemittances.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.successReceiveRemittances.params.cleverTap,
          [getFormattedDateTimes()]
        ),
      },
    });
  }
}
