import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { IonicModule } from '@ionic/angular';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { FeaturesProfileModule } from '@nequi/features/profile/presentation';
import { NequiComponentsModule, NequiUiModule } from '@nequi/ui';
import { TranslateModule, TranslatePipe } from '@ngx-translate/core';

import { RemittancesConfig } from '../config/remittances.config';
import { RemittancesInteractor, RemittancesRepository } from '../core';
import { BottomsheetComponent } from './components/bottomsheet/bottomsheet.component';
import { FeaturesRemittancesRoutingModule } from './features-remittances-routing.module';
import { FormPage } from './pages/form/form.page';
import { TermsPage } from './pages/terms/terms.page';

@NgModule({
  declarations: [FormPage, TermsPage, BottomsheetComponent],
  imports: [
    CommonModule,
    IonicModule,
    TranslateModule,
    NequiComponentsModule,
    FeaturesRemittancesRoutingModule,
    FeaturesProfileModule,
    NequiUiModule,
  ],
  providers: [
    {
      provide: RemittancesInteractor,
      deps: [RemittancesRepository],
      useClass: RemittancesInteractor,
    },
    {
      provide: ModuleConfigProvider,
      useValue: RemittancesConfig,
    },
    TranslatePipe,
  ],
})
export class FeaturesRemittancesModule {}
